<template>
  <div v-bind:style="{ margin: 'auto', height: conf.height, width: conf.width}">
    <canvas></canvas>
  </div>
</template>

<script>
  import Chart from 'chart.js'

  function needAxes(type) {
    return type == 'bar' || type == 'horizontalBar' || type == 'line'
  }

  export default {
    props: ['chartData', 'chartConf'],
    data() {
      return {
        ctx: null,
        chart: null,
        conf: {
          width: this.chartConf ? (this.chartConf.width ? this.chartConf.width : '100%') : '100%',
          height: this.chartConf ? (this.chartConf.height ? this.chartConf.height : '400px') : '400px',
          type: 'bar',
          label: 'Chart',
          hasValueLabel: true,
          backgroundColor: 'rgba(54, 162, 235, 0.2)',
          borderColor: 'rgba(54, 162, 235, 1)',
          borderWidth: 1,
          pointBorderColor: 'rgba(54, 162, 235, 1)',
          pointBorderWidth: 1,
          pointBackgroundColor: 'rgba(54, 162, 235, 1)'
        }
      }
    },
    methods: {
      renderChart() {
        var conf = Object.assign({}, this.conf, this.chartConf)
        var data = {
          labels: [],
          datasets: [{
            label: conf.label,
            data: [],
            backgroundColor: conf.backgroundColor,
            borderColor: conf.borderColor,
            borderWidth: conf.borderWidth,
            pointBorderColor: conf.pointBorderColor,
            pointBorderWidth: conf.pointBorderWidth,
            pointBackgroundColor: conf.pointBackgroundColor
          }],
        }
        var excludeAttrs = ['width', 'height', 'hasValueLabel']
        Object.keys(conf).forEach(confKey => {
          if (excludeAttrs.indexOf(confKey) < 0) {
            data.datasets[0][confKey] = conf[confKey]
          }
        })
        
        if (this.chartData instanceof Array) {
          data.labels = this.chartData.map(d => Object.keys(d)[0])
          data.datasets[0].data = this.chartData.map(d => d[Object.keys(d)[0]])
        }
        else {
          data.labels = Object.keys(this.chartData)
          data.datasets[0].data = Object.keys(this.chartData).map(k => this.chartData[k])
          data.datasets[0].scaleOverride = true
        }

        this.chart = new Chart(this.$el.children[0], {
          type: conf.type,
          data: data,
          options: {
            responsive: true,
            maintainAspectRatio: false,
            scale: conf.type == 'radar' ? { ticks: { beginAtZero: true } } : {},
            scales: {
              xAxes: needAxes(conf.type) ? [{ ticks: { beginAtZero : true, min: 0 } }] : [],
              yAxes: needAxes(conf.type) ? [{ ticks: { beginAtZero : true, min: 0 } }] : []
            },
            hover: conf.hasValueLabel ? { animationDuration: 0 } : {},
            animation: conf.hasValueLabel ? {
              duration: 1,
              onComplete: function() {
                var ctx = this.chart.ctx
                ctx.font = Chart.helpers.fontString(Chart.defaults.global.defaultFontSize, 'normal', Chart.defaults.global.defaultFontFamily)
                ctx.fillStyle = this.chart.config.options.defaultFontColor
                ctx.textAlign = 'center'
                ctx.textBaseline = 'bottom'
                this.data.datasets.forEach(dataset => {
                  for (var i = 0; i < dataset.data.length; i++) {
                    var model = dataset._meta[Object.keys(dataset._meta)[0]].data[i]._model
                    ctx.fillText(dataset.data[i], model.x, model.y - 5)
                  }
                })
              }
            } : {}
          }
        })
      }
    },
    mounted() {
      Chart.defaults.global.title.display = true
      Chart.defaults.global.legend.position = 'bottom'
      Chart.defaults.global.legend.onClick = () => false
      this.renderChart()
    },
    watch: {
      chartData() {
        this.chart.destroy()
        this.renderChart()
      }
    }
  }
</script>

<style scoped>

</style>


