<template>
  <div>
    <main class="app">
      <section class="sec-zone">
        <div class="sec-panel">
          <h3>Average Price: {{chartData.avgPrice}}</h3>
          <transition name="fade">
            <heat-map v-if="mapLoaded"
              :mapData="mapData"
              :mapConf="mapConf">
            </heat-map>
          </transition>
        </div>
      </section>
      <section class="sec-zone">
        <div class="sec-panel">
          <div class="profile-group">
            <simple-chart
              :chartData="chartData.types"
              :chartConf="typeChartConf">
            </simple-chart>
            <simple-chart
              :chartData="chartData.sizes"
              :chartConf="sizeChartConf">
            </simple-chart>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
  import 'normalize.css'
  import HeatMap from './HeatMap'
  import SimpleChart from './SimpleChart'

  const chartConfBase = {
    type: "bar",
    width: "6.5rem",
    height: "3.8rem",
    hasValueLabel: false,
    backgroundColor: "#4ca4ff"
  }

  export default {
    name: 'App',
    created () {
      this.$http.get('chart.json').then(resp => {
        this.chartData = resp.body
      })
      this.$http.get('map.json').then(resp => {
        let tmpData = resp.body.filter(item => {
          return item.location !== undefined && item.location !== null
        }).map(item => {
          return [
            item.location.lat,
            item.location.lng,
            item.avgPrice / 10000
          ]
        })
        this.mapData = tmpData
        this.mapLoaded = true
      })
    },
    data () {
      return {
        chartData: {
          types: {},
          sizes: [],
          avgSize: "",
          avgPrice: ""
        },
        typeChartConf: Object.assign({ label: 'Type Count' }, chartConfBase),
        sizeChartConf: Object.assign({ label: 'Size Count' }, chartConfBase),
        mapLoaded: false,
        mapData: [],
        mapConf: {
          width: '100%',
          height: '6rem',
          // maxZoom: 8,
          minZoom: 12,
          bounds: [[31.74, 117.09], [31.94, 117.40]]
        },
      }
    },
    methods: {

    },
    components: {
      HeatMap,
      SimpleChart
    }
  }
</script>

<style lang="scss">
  @import "app/scss/variables";
  @import "app/scss/mixins";
  @import "app/scss/base";

  /* Flex  */
  .app {
    width: 10rem;
    margin: .7rem auto;
    justify-content: space-around;
  }
  .sec-panel {
    width: 100%;
    padding: .3rem;
    padding-top: .2rem;
    border: 1px #ddd solid;
    border-top: 4px $color-info solid;
    box-shadow: 0 3px 8px #ddd;
  }
  h3 {
    color: $color-info;
  }
  .sec-panel-title {
    @include flex-center-justify();
    padding: auto .15rem .2rem .15rem;
    margin-bottom: .15rem;
    height: .4rem;
    width: 100%;
    color: #666;
  }
  .sec-panel-h1 {
    position: relative;
    top: .03rem;
    color: $color-info;
    font-size: .18rem;
    margin-right: .1rem;
  }
  .sec-panel-group {
    display: flex;
    align-items: center;
  }
  .sec-zone {
    margin-top: .25rem;
  }

  .profile-group {
    width: 100%;
    @include flex-center-justify();
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s ease;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
</style>



