<template>
  <div class="bmap-container"  v-bind:style="{ height: height, width: width}">
    <transition name="fade">
      <div class="bmap-info" v-if="hasInfo">
        {{info}}
      </div>
    </transition>
  </div>
</template>

<script>
  import 'leaflet/dist/leaflet.css'
  import L from 'leaflet'
  import heat from 'leaflet.heat'

  const initConf = {
    width: '100%',
    height: '550px',
    minZoom: 3,
    hasZoomControl: true,
    hasTileLayer: true
  }
  var conf = {}

  export default {
    props: ['mapData', 'mapConf'],
    created () { conf = Object.assign({}, initConf, this.mapConf) },
    data () {
      return {
        width: this.mapConf.width ? this.mapConf.width : this.initConf.width,
        height: this.mapConf.height ? this.mapConf.height : this.initConf.height,
        hasInfo: false,
        info: ''
      }
    },
    methods: {
      showInfo (e) {
        this.hasInfo = true
        this.info = e.target.options.text
      },
      hideInfo (e) {
        this.hasInfo = false
      }
    },
    mounted () {
      var vm = this
      var map = L.map(this.$el, {
        attributionControl: false,
        zoomControl: conf.hasZoomControl,
        maxBounds: conf.bounds,
        minZoom: conf.minZoom
      }).fitBounds(conf.bounds)

      if (conf.hasTileLayer) {
        L.tileLayer('//{s}.tile.osm.org/{z}/{x}/{y}.png', {}).addTo(map)
      }
      let data = JSON.parse(JSON.stringify(this.mapData))
      var heat = L.heatLayer(data.map(d => {
        return [d[0], d[1], d[2] * 1.5]
      }), { radius: 40 }).addTo(map)
      data.forEach(item => {
        if (item[2] > 0) {
          L.circle([item[0], item[1]], {
            color: 'transparent',
            stroke: false,
            radius: item[2] * 1000,
            text: 'Price: ' + parseInt(item[2] * 10000)
          }).on("mouseover", vm.showInfo)
            .on("mouseout", vm.hideInfo)
            .addTo(map)
        }
      })
    }
  }
</script>

<style lang="scss" scoped>
  .bmap-container {
    position: relative;
    margin: 0 auto;
  }
  .bmap-info {
    position: absolute;
    top: .15rem;
    right: .1rem;
    height: .45rem;
    padding: .05rem .15rem;
    font: .14rem Arial;
    line-height: .45rem;
    vertical-align: middle;
    box-shadow: 0 2px 5px grey;
    color: #666;
    background: white;
    z-index: 999;
    cursor: pointer;
  }

  .leaflet-container {
    background: transparent !important;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .4s ease;
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
</style>



